"use client"

import React, { useEffect, useRef } from "react"
import * as THREE from "three"
import ThreeGlobe from "three-globe"
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls"
import type { Hotspot } from "@/lib/types"
import type { SatellitePosition } from "@/lib/orbital"

interface EarthGlobeProps {
  launchSite?: { lat: number; lon: number; name?: string } | null
  corridorPath?: { lat: number; lon: number }[]
  corridorWidth?: number
  hotspots?: Hotspot[] | null
  showCorridor?: boolean
  showHotspots?: boolean
  showSatellites?: boolean
  satellites?: SatellitePosition[]
  compareCorridorPath?: { lat: number; lon: number }[] | null
  onHotspotHover?: (h: Hotspot | null) => void
}

export default function EarthGlobe({
  launchSite,
  hotspots = null,
  satellites = [],
}: EarthGlobeProps) {
  const mountRef = useRef<HTMLDivElement | null>(null)
  const globeRef = useRef<any>(null)
  const animRef = useRef<number | null>(null)

  useEffect(() => {
    if (!mountRef.current) return

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(45, mountRef.current.clientWidth / mountRef.current.clientHeight, 0.1, 1000)
    camera.position.z = 300

    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })
    renderer.setSize(mountRef.current.clientWidth, mountRef.current.clientHeight)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    mountRef.current.innerHTML = ""
    mountRef.current.appendChild(renderer.domElement)

    // lights
    scene.add(new THREE.AmbientLight(0xbbbbbb))
    const dir = new THREE.DirectionalLight(0xffffff, 0.6)
    dir.position.set(5, 3, 5)
    scene.add(dir)

    // three-globe
    const globe = new ThreeGlobe()
    globe.globeImageUrl('//unpkg.com/three-globe/example/img/earth-dark.jpg')
    globe.showAtmosphere(true)
    globe.atmosphereColor('#3fb0ff')
    globe.atmosphereAltitude(0.25)

    globe.pointsData([])
    scene.add(globe)
    globeRef.current = globe

    // controls
    const controls = new OrbitControls(camera, renderer.domElement)
    controls.autoRotate = true
    controls.autoRotateSpeed = 0.25
    controls.enablePan = false

    const handleResize = () => {
      if (!mountRef.current) return
      const w = mountRef.current.clientWidth
      const h = mountRef.current.clientHeight
      camera.aspect = w / h
      camera.updateProjectionMatrix()
      renderer.setSize(w, h)
    }
    window.addEventListener('resize', handleResize)

    const animate = () => {
      controls.update()
      renderer.render(scene, camera)
      animRef.current = requestAnimationFrame(animate)
    }
    animate()

    return () => {
      if (animRef.current) cancelAnimationFrame(animRef.current)
      window.removeEventListener('resize', handleResize)
      controls.dispose()
      renderer.dispose()
      scene.clear()
      if (mountRef.current) mountRef.current.innerHTML = ''
    }
  }, [])

  // update points when satellites or hotspots change
  useEffect(() => {
    const globe = globeRef.current as any
    if (!globe) return

    // prepare satellite points
    const satPoints = (satellites ?? []).map((s) => ({
      lat: s.lat,
      lng: s.lon,
      size: Math.max(0.2, Math.min(1.5, ((s.altitudeKm ?? 400) - 200) / 1000)),
      color: s.type === 'debris' ? 'rgba(255,120,80,0.95)' : s.type === 'station' ? 'rgba(120,200,255,0.95)' : 'rgba(120,220,120,0.9)',
      name: s.name,
    }))

    // prepare hotspots as points too (different color/size)
    const hotPoints = (hotspots ?? []).map((h) => ({
      lat: h.lat,
      lng: h.lon,
      size: Math.max(0.4, Math.min(2.0, (h.weight ?? 0.2) * 2)),
      color: h.weight && h.weight > 0.7 ? 'rgba(255,60,60,0.95)' : 'rgba(255,200,60,0.9)',
      name: h.label,
    }))

    // combine and set as pointsData
    const points = [...satPoints, ...hotPoints]
    globe
      .pointsData(points)
      .pointLat((d: any) => d.lat)
      .pointLng((d: any) => d.lng)
      .pointColor((d: any) => d.color)
      .pointAltitude((d: any) => 0.002 + (d.size || 0.5) * 0.001)
      .pointRadius((d: any) => (d.size || 0.5) * 0.45)

  }, [satellites, hotspots])

  return (
    <div ref={mountRef} style={{ width: '100%', height: 600, maxHeight: '80vh', borderRadius: 8, overflow: 'hidden' }} />
  )
}
